import React from 'react';
import './PricePill.css';

const PricePill = ({ price, hasDiscount=null, discountedPrice=null, discountPercent=null, currencyCode='USD', locale='en-US', showTail, classes }) => {
  const formattedCurrencyString = (amount, currencyCode='USD', locale='en-US') => {
      const options = {
        style: 'currency',
        currency: currencyCode,
        minimumFractionDigits: 2,
        maximumFractionDigits: 2,
      };
      
      if (!!(typeof Intl === 'object' && Intl && typeof Intl.NumberFormat === 'function')) {
        const numberFormatter = new Intl.NumberFormat(locale, options);
        return numberFormatter.format(amount);
      }
      
      return amount.toLocaleString(locale, options);
  }

  const CurrencyView = () => (<span>{ (showTail) ? currencyCode : '' }</span>);
  const OrdinaryView = () => (<span className={`chip white darken-2 green-text${ ` ${ classes }` }`} style={{ border: '1px solid green', verticalAlign: 'middle' }}>
                                <b>{ formattedCurrencyString(price, currencyCode, locale) }</b> <CurrencyView /></span>);
  const DiscountView = () => (<span className={`chip white darken-2 red-text${ ` ${ classes }` }`} style={{ border: '1px solid red', verticalAlign: 'middle' }}><b>{ formattedCurrencyString(discountedPrice, currencyCode, locale) } </b><CurrencyView /> { (discountPercent) ? <small><em><del style={{ color: '#444' }}>{`${ formattedCurrencyString(price, currencyCode, locale) }`}</del></em></small> : ''}</span>);

  return (hasDiscount) ? <DiscountView /> : <OrdinaryView />;
};

export default PricePill;



