import {
  UPDATE_CART,
  ADD_ITEM_CART,
  REMOVE_ITEM_CART,
  INCREMENT_ITEM_QTY_CART,
  DECREMENT_ITEM_QTY_CART,
  CLEAR_ALL_CART,
} from './../actions';

export default function(state = {}, action) {
  switch (action.type) {
    case UPDATE_CART: {
      const data = action.payload;
      if (typeof data === 'string') return { status: 'error', code: -1, message: data };
      return data;
    }
    case ADD_ITEM_CART: {
      const data = action.payload;
      if (typeof data === 'string') return { status: 'error', code: -1, message: data };
      return data;
    }
    case REMOVE_ITEM_CART: {
      const data = action.payload;
      if (typeof data === 'string') return { status: 'error', code: -1, message: data };
      return data;
    }
    case INCREMENT_ITEM_QTY_CART: {
      const data = action.payload;
      if (typeof data === 'string') return { status: 'error', code: -1, message: data };
      return data;
    }
    case DECREMENT_ITEM_QTY_CART: {
      const data = action.payload;
      if (typeof data === 'string') return { status: 'error', code: -1, message: data };
      return data;
    }
    case CLEAR_ALL_CART: {
      const data = action.payload;
      if (typeof data === 'string') return { status: 'error', code: -1, message: data };
      return data;
    }
    default: {
      return state;
    }
  }
}



