import React, { Component } from 'react';
import './Product.css';
import PricePill from '../PricePill';

const SaleView = ({ discountPercentRate=0 }) => {
  const percentText = (Math.round( (discountPercentRate - 1) * 100 )).toString();
  const saleText = `Sale! ${ percentText }% off!`;
  return (<span className="sale-view red"><i className="material-icons">loyalty</i> <span className='sale-text'><b>{ saleText }</b></span></span>);
};

class Product extends Component {
  
  constructor(props) {
    super(props);

    this.state = {
    };
    
    this.handleOnClick = this.handleOnClick.bind(this);
  }

  handleOnClick(e) {
    e.preventDefault();
    const { id } = this.props;
    this.props.onClickItem(e, id);
  }

  render() {
    const { id,
      name='Untitled Product',
      desc=null,
      price=0,
      discountedPrice=null,
      discountPercent=1,
      hasDiscount=false,
      imagePath=null,
      currencyCode='USD',
      locale='en-US' } = this.props;
    
    return (<div className="col s12 m6 l4 product-view">
            <div className="card hoverable">
                <div className="card-image">
                    { (hasDiscount) ? <SaleView discountPercentRate={ discountPercent } /> : '' }
                    <img src={ imagePath || 'https://picsum.photos/640/400?image=94&blur' } alt="Product" />
                    <span className="card-title pink-text text-lighten-2"><span className="title col s6 l12">{ name }</span>&nbsp;
                          <small><PricePill price={ price } currencyCode={ currencyCode } hasDiscount={ hasDiscount } discountedPrice={ discountedPrice } discountPercent={ discountPercent } /></small>
                    </span>
                    <a id={id} role="button" onClick={ this.handleOnClick } className="btn-floating halfway-fab waves-effect waves-light green">
                      <i className="material-icons">add_shopping_cart</i>
                    </a>
                </div>
                <div className="card-content">
                    <p>{ desc || 'No description.' }</p>
                </div>
            </div>
          </div>);
  }
}

export default Product;



