import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import './CartListItem.css';
import { formattedCurrencyString } from '../ShoppingCart/ShoppingCart';
import {
  updateCart,
  incrementFromCart,
  decrementFromCart,
  removeFromCart,
} from './../actions';

class CartListItem extends Component {
  
  constructor(props) {
    super(props);

    this.changeQuantity = this.changeQuantity.bind(this);
    this.removeProduct = this.removeProduct.bind(this);
  }

  changeQuantity(id, tag, e) {
    e.preventDefault();
    const completion = () => { this.checkIfUpdateIsNeeded(); };
    (tag === 'add') ? this.props.incrementFromCart(id, this.props.updateCart.bind(this, completion)) : this.props.decrementFromCart(id, this.props.updateCart.bind(this, completion));
  };
  
  removeProduct(id, e) {
    e.preventDefault();
    const completion = () => {  };
    if (window.confirm(`⚠️\tThis will remove "${ this.props.product.name }"...\n\tAre you absolutely sure?`)) { this.props.removeFromCart(id, this.props.updateCart.bind(this, completion)) }
  };

  checkIfUpdateIsNeeded () {
  }

  render() {
    const {
      product: {
      id=null,
      name=null,
      quantity=0,
      subTotal=0,
      inStockCount=999,
      count=0,
      hasDiscount=false,
      discountedPrice=0,
      price=0,
      },
      rowNumber,
    } = this.props;

    return (<tr> 
          <td style={{ padding: 10 }} width="28" className="center">
              <button onClick={this.removeProduct.bind(this, id)} title="Remove" data-toggle="tooltip" data-placement="right" className="btn btn-flat red-text text-darken-3 hasTooltip">
                  <span className="material-icons" style={{ verticalAlign: 'middle' }}>delete</span>
              </button>
          </td>
          <td style={{ padding: 10 }} width="20" className="text-centercenter"><span style={{ color: '#777' }} ><b>{ rowNumber }</b></span></td>
          <td style={{ padding: 10 }} width="55" className="center"><span style={{ fontStyle: 'italic', color: 'gray' }} className="center">{ id }</span></td>
          <td style={{ padding: 10 }} width="180" className="text-left"><span><b><a href="./products?id=0">{ name }</a></b></span></td> 
          <td style={{ padding: 10, minWidth: 180 }} align="center" valign="middle" width="180" className="text-center">
              <div className="form-horizontal">
                  <div className="input-group"> 
                      <div className="gwCartQtyBtn number form-control center"> { parseInt(quantity).toString() || 1 }</div>

                      <span className="input-group-addon">
                      <button disabled={ (quantity <= 1) ? true : '' } onClick={ this.changeQuantity.bind(this, parseInt(id), 'remove') } title="Remove one" className="btn orange">
                              <span className="material-icons" style={{ verticalAlign: 'middle' }}>remove</span>
                          </button><button disabled={ (quantity >= inStockCount) ? 'disabled' : '' } onClick={ this.changeQuantity.bind(this, parseInt(id), 'add') } title="Add one more" className="btn green">
                              <span className="material-icons" style={{ verticalAlign: 'middle' }}>add</span>
                          </button>
                      </span> 
                  </div>
              </div>
          </td>
          <td width="90" className="center"><span className={`center${ (hasDiscount) ? ' pink-text' : '' }`}><em>{ formattedCurrencyString((hasDiscount) ? discountedPrice : price) }</em></span></td> 
          <td width="90" className="center"><span><strong>{ formattedCurrencyString(subTotal) }</strong></span></td> 
      </tr>);
    }
};

const mapDispatchToProps = (dispatch) => bindActionCreators({
  updateCart,
  incrementFromCart,
  decrementFromCart,
  removeFromCart,
}, dispatch);

export default connect(null, mapDispatchToProps)(CartListItem);



