import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import {
  updateCart,
} from './../actions';
import './ShoppingCart.css';
import ShoppingCartView from '../ShoppingCartView';

export const formattedCurrencyString = (amount, currencyCode='USD', locale='en-US') => {
  const options = {
    style: 'currency',
    currency: currencyCode,
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  };
  
  if (!!(typeof Intl === 'object' && Intl && typeof Intl.NumberFormat === 'function')) {
    const numberFormatter = new Intl.NumberFormat(locale, options);
    return numberFormatter.format(amount);
  }
  
  return amount.toLocaleString(locale, options);
}

class ShoppingCart extends Component {
  
  constructor(props) {
    super(props);

    const { shoppingCart } = this.props;
    
    this.state = {
        shoppingCart: (shoppingCart.count) ? shoppingCart  : { status: 'loading', count: 0, totalSum: 0.0 },
    };

    this.fetchData = this.fetchData.bind(this);
    this.updateUI = this.updateUI.bind(this);
    
    setInterval(this.fetchData, 60 * 1000);
    this.fetchData();
  }

  componentDidMount() {
    // this.fetchData();
  }

  componentWillUpdate(nextProps, nextState, nextContext) {
    var _self = this;
    if (nextProps.shoppingCart !== _self.props.shoppingCart) {
      this.setState({ loading: true, shoppingCart: nextProps.shoppingCart || { count: 0, totalSum: 0.0 } });
    }
  }

  fetchData() {
    this.props.updateCart(this.updateUI);
  }

  updateUI() {
    const shoppingCart = this.props.shoppingCart || { status: 'loading', count: 0, totalSum: 0.0 };
    if (shoppingCart && typeof shoppingCart === 'object' && shoppingCart.constructor.name === 'Error') {
      this.setState({ shoppingCart: { status: 'error', count: 0, totalSum: 0.0 }});
      return;
    }
    this.setState({ shoppingCart });
  }

  render() {
    if ((this.state.shoppingCart.status === 'loading') ) return (<div className="card center">Loading...</div>);
    return (<div className="row">
      <ShoppingCartView shoppingCart={ this.props.shoppingCart } />
      { <a role="button" href="#!" onClick={ (this.props.shoppingCart.count > 0) ? (e) => { window.alert('That\'s it for now. Thanks for viewing my demo. 😉\n\nMore features to implement:\n\nTODO: Add discount codes to cart.\nTODO: Validate, and show steps: Payment, send order to server (store in SQL db), show a summary.\nTODO: Display previous orders on user\'s account page.\nTODO: Admin page.\netc.'); } : '' } disabled={ (this.props.shoppingCart.count <= 0 || !(this.props.shoppingCart.order)) ? 'disabled' : '' } className="waves-effect waves-ripple btn btn-large green lighten-1 white-text right" style={{ verticalAlign: 'middle' }}><span style={{ verticalAlign: 'middle' }}>Checkout</span> <i className="material-icons" style={{ verticalAlign: 'top' }}>arrow_right</i></a> }
    </div>);
    }
}

const mapDispatchToProps = dispatch => bindActionCreators({
  updateCart,
}, dispatch);

function mapStateToProps({ shoppingCart }) {
  return { shoppingCart };
}

export default connect(mapStateToProps, mapDispatchToProps)(ShoppingCart);



