import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import _ from 'lodash';
import './ShoppingCartView.css';
import {
  updateCart,
  clearAllFromCart,
} from './../actions';
import CartListItem from '../CartListItem';
import { formattedCurrencyString } from '../ShoppingCart/ShoppingCart';

class ShoppingCartView extends Component {

  constructor(props) {
    super(props);
    this.state = {
      ...props,
    }
    this.clearAllProducts = this.clearAllProducts.bind(this);
  }

  clearAllProducts(e) {
    e.preventDefault();
    if(window.confirm('⚠️\tAre you absolutely sure that you\n\twant to remove all your items?')) {
      const completion = () => {  };
      this.props.clearAllFromCart(this.props.updateCart.bind(this, completion));
    }
  }

  render() {
    const { shoppingCart } = this.props;
    if (!(typeof shoppingCart === 'object')) return <div className="card center">Loading...</div>;
    window.__prev_cart__ = shoppingCart || { count: window.__prev_cart__.totalSum || 0, totalSum: window.__prev_cart__.totalSum || 0 };
    const { order = { orderProducts: _.range(0, (window.__prev_prod__ || []).length || 0).map(()=> new Object()) }, count=window.__prev_cart__.count || 0 , totalSum=window.__prev_cart__.totalSum || 0, vat=1.25 } = shoppingCart;
    
    const products = order.orderProducts;
    window.__prev_prod__ = products || [];

    const NoItemsView = () => {
      return (
        <div>
          <h4>No items... Let's get shopping!</h4>
        </div>
      );
    }

    const EmptyRows = () => {
      return (<tr><td className="center" colSpan={7}><br/><em>Your cart is empty.</em><br/><br/></td></tr>);
    }

    const vatPercent = parseInt((parseFloat(vat) - 1) * 100);
    let rowNumber = 0;
    const TableView = () => {
      return (<div className="table-responsive" style={{ width: '100%', minWidth: 600 }}>
          <table className="table highlight centered">
              <thead>
                  <tr>
                      <th valign="middle" width="90">
                          <button title="Clear all products" onClick={ this.clearAllProducts.bind(this) } data-toggle="tooltip" data-placement="top" className="btn btn-flat has-tooltip">
                              <span className="material-icons" style={{ verticalAlign: 'middle' }}>delete</span>
                          </button>
                      </th>
                      <th valign="middle"><b>#</b></th>
                      <th valign="middle"><b>Pid.</b></th>
                      <th valign="middle"><b>Product</b></th>
                      <th valign="middle"><b>Qty.</b></th>
                      <th valign="middle"><b>Price</b></th>
                      <th valign="middle"><b>Subtotal</b></th>
                  </tr>
              </thead>
              <tbody>
                  { (products.length > 0) ? products.map((product) => <CartListItem key={ product.id } rowNumber={ ++rowNumber } product={ product } /> ) : <EmptyRows /> }
              </tbody>
          </table>
          <br />
          <hr />
          <div className="text-right">
              <h3 className="right" style={{ borderRadius: 10, minHeight: 32 }}>Total <span className="green-text">{ formattedCurrencyString(parseFloat(totalSum || 0).toString()) }</span><small><small><small><em className="grey-text">(price incl. VAT { vatPercent }%)</em></small></small></small></h3>
          </div>
      </div>);
    }

    return (<div className="col s12 m12 l12">
    <div style={{ width: '100%' }}>
        <div  className="panel panel-default">
            <div className="panel-heading">
                <h3 className="panel-title pink-text">
                  <span className="material-icons" style={{ verticalAlign: 'middle', fontSize: 42 }}>shopping_cart</span>
                  <span style={{ verticalAlign: 'middle' }}>Shopping Cart</span><span className="chip pink white-text right">{ count } Items</span>
                </h3>
            </div>
            <div style={{ overflowX: 'auto', width: '100%' }}>
              { <TableView /> }
              <a role="button" href="./products" title="Continue Shopping" className="waves-effect waves-ripple btn pink lighten-1 white-text" style={{ verticalAlign: 'middle' }}><span style={{ verticalAlign: 'middle' }}>Continue Shopping</span> <i className="material-icons" style={{ verticalAlign: 'top' }}>shopping_basket</i></a>
            </div>
        </div>
    </div>
  </div>);
  }
}

const shopcartViewMapDispatchToProps = dispatch => bindActionCreators({
  updateCart,
  clearAllFromCart,
}, dispatch);

export default connect(null, shopcartViewMapDispatchToProps)(ShoppingCartView);



