import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { getAllProducts, addToCart, updateCart } from './../actions';
import './ProductsPage.css';
import Product from '../Product';

class ProductsPage extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      products: [],
      loading: true,
    };

    this.fetchData = this.fetchData.bind(this);
    this.updateUI = this.updateUI.bind(this);
    this.addProduct = this.addProduct.bind(this);
    this.fetchData();
  }
  
  componentDidMount() {
    // this.fetchData();
  }
  
  componentWillUpdate(nextProps, nextState, nextContext) {
    const _self = this;
    const { products } = nextProps;
    if ((typeof products === 'object' && products.constructor.name === 'Error')) {
        // Execute only on error.
        requestAnimationFrame(() => {
          // We'll only execute this code when the user is viewing the site (green).
          if (_self.timeoutId !== null) clearTimeout(_self.timeoutId);
          _self.timeoutId = setTimeout(_self.fetchData, 5 * 1000); // retry in 5 seconds.
        }, _self);
    }
    
    if (!(typeof products === 'object' && products.constructor.name === 'Error')) {
        if (_self.timeoutId !== null) clearTimeout(_self.timeoutId);
    }
  }

  fetchData() {
    this.setState({ loading: true, error: false });
    this.props.getAllProducts(this.updateUI);
  }

  addProduct(e, id) {
    e.preventDefault();
    const product = this.props.products.filter(item => item.id === id)[0] || { name: null };
    const productName = (product.name) ? `– "${ product.name.trim().substring(0, 16) }${ (product.name.trim().length > 16) ? ' ...' : '' }"` : '';
    this.props.addToCart(id, () => {
      // console.log('Added to cart');
      this.props.updateCart(() => {
        // console.log('Updating Cart')
        requestAnimationFrame(() => {
          const message = `Product added! ${ productName }`;
          const symbols = '<span class="green-text">add_shopping_cart</span>';
          const toastView = `<span onmouseup="(function() { window.location='./checkout'; })();" style="cursor:pointer;"><span><i class="material-icons" style="vertical-align: middle;">${ symbols }</i> <span style="vertical-align: middle;">${ message }</span></span></a>`;
          /* global Materialize */
          if (typeof Materialize === 'object') new Materialize.Toast(toastView, 2000);
        });
      });
    });
  }

  updateUI() {
    const { products } = this.state;
    if (typeof products === 'object' && products.constructor.name === 'Error') {
      this.setState({ products: [], error: true });
      return;
    }
    clearTimeout(this.timeoutId || 0);
    this.setState({ loading: false, error: false, products: this.props.products || [] });
  }

  render() {
    const ProductListHeader = ({ title='Products', symbol='list', loading }) => (
      <ul className={`collection with-header card col s12 l12 product-list-header${`${(loading) ? '-loading' : '' }`}`}>
        <li className="collection-header" style={{ borderBottom: 0 }}>
            <h4 className="pink-text">
              <i className="material-icons pink-text"
                 style={{ fontSize: 36, verticalAlign: 'top' }}>{ symbol }</i> {title}
            </h4>
        </li>
      </ul>);
    
    const { loading, products } = this.state;
    const error = (typeof products === 'object' && products.constructor.name === 'Error');

    if (this.timeoutId !== null) clearTimeout(this.timeoutId);
    if (loading === true || error === true) {
      
      return (error !== true) ? (<div className="row"><ProductListHeader loading /><div className="progress pink lighten-3"><div className="indeterminate pink"></div></div><div className="card col s12 m12 l12 center pink-text" style={{ padding: 10 }}><i className="material-icons" style={{ verticalAlign: 'bottom' }}>cloud_download</i> Loading products... <i className="material-icons" style={{ verticalAlign: 'bottom' }}></i></div></div>) :  (<div className="row"><ProductListHeader loading /><div className="progress pink lighten-3"><div className="indeterminate pink"></div></div><div className="card col l12 center pink lighten-5 pink-text" style={{ padding: 10 }}><i className="material-icons" style={{ verticalAlign: 'bottom' }}>sync_problem</i> Trying to reconnect to the server...<i className="material-icons" style={{ verticalAlign: 'bottom' }}>router</i></div></div>);
    }
    
    return (<div className="row">
      {/* <button className="waves-effect waves-ripple btn btn-large" onClick={this.addProduct}>Add product</button> */}
      <ProductListHeader />
      {
        (typeof products.map === 'function' && products.length > 0) ? products.map((props) => <Product key={props.id.toString()} onClickItem={this.addProduct} {...props} />) : <div className="card col s12 m12 l12 center pink-text" style={{ padding: 10, minWidth: '100%' }}><i className="material-icons pink-text" style={{ verticalAlign: 'bottom' }}>info</i> No products available.<span role="img" aria-label="Shocked cat emoji">🙀</span></div>
      }
    </div>);
  }
}

const mapDispatchToProps = dispatch => bindActionCreators({ addToCart, getAllProducts, updateCart }, dispatch);

function mapStateToProps({ products }) {
  return { products };
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductsPage);



