import React from 'react';
import ShoppingCartMenuItem from '../ShoppingCartMenuItem';

class Navbar extends React.Component {
    
    render() {
        const isCurrentPage = (page) => {
            const loc = window.location.toString();
            let result = false;
            switch (page) {
                case 'home':
                    result = (loc.endsWith('/'));
                    break;
                case 'products':
                    result = (loc.indexOf('/products') !== -1);
                    break;
                case 'contact':
                    result = (loc.indexOf('/contact') !== -1);
                    break;
                case 'checkout':
                    result = (loc.indexOf('/checkout') !== -1);
                    break;
                default:
                    return result;
            }
            
            return result ? 'active' : '';
        };
        return (<div className="navbar-fixed">
            <nav className="pink lighten-3"> 
                <div className="nav-wrapper"> 
                    <a href="./" className="brand-logo" style={{ paddingLeft: 16 }}><i className="material-icons left" style={{ fontSize: 48 }}>store</i><span style={{ fontFamily: "Ceviche One",
        marginLeft: -14 }}>My Shop</span></a>
                    <ul id="nav-mobile" className="right hide-on-med-and-down">
                        <li className={ isCurrentPage('home') ? 'active' : '' }>
                            <a href="./">Home</a>
                        </li>
                        <li className={ isCurrentPage('products') }>
                            <a href="./products">Products</a>
                        </li>
                        <li className={ isCurrentPage('contact') ? 'active' : '' }>
                            <a href="./contact">Contact</a>
                        </li>
                        <li className={ isCurrentPage('checkout') ? 'active' : '' }>
                        <ShoppingCartMenuItem />
                        <ul id="dropdown1" className="dropdown-content" style={{ minWidth: 200 }}>
                            <li>
                                <a href="./checkout" className="green-text right-align"><span style={{ marginRight: 8, verticalAlign: 'top' }}>Checkout</span> <i className="material-icons right">shopping_basketarrow_right</i></a>
                            </li>
                            <li>
                                <a href="#!" className="orange-text right-align">Continue Shopping<i className="material-icons right"></i></a>
                            </li>
                        </ul>
                        </li>
                    </ul>
                </div>                 
            </nav>
        </div>);
    }
}

export default Navbar;



