import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import ReduxPromise from 'redux-promise';
import ReduxThunk from 'redux-thunk';
import smoothStorage from 'redux-smooth-storage';
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';
import reducers from './reducers';

// Smooth Storage
const sStorage = smoothStorage('myshop_store', 'sessionStorage');
const initialState = {};
const { watch, hydrateState } = sStorage;
const getStateSotrage = () => hydrateState() || initialState;

const middlewares = [watch, ReduxThunk, ReduxPromise];
const createStoreWithMiddleware = applyMiddleware(...middlewares)(createStore);
const store = createStoreWithMiddleware(reducers, getStateSotrage());

ReactDOM.render(<Provider store={store}><App /></Provider>, document.getElementById('root'));
registerServiceWorker();



