import React from 'react';
import './HomePage.css';
import PricePill from './../PricePill';

const HomePage = () => {

    const action = () => {
        if(window.confirm('Whoops, Decoy! 😂\n\nThis is just a demo button, head straight off to the Products section to dive into the real action:\n\n• "OK" will lead you to the real action.\n• "Cancel" will dismiss this box.')) {
            window.location= `./products`;
        }
    };

  return (<div className="row">
    <div className="col s12 m12 l4"> 
          <h3 className="pink-text">Let's shop!</h3> 
          <p>Cras justo odio, dapibus ac facilisis in, egestas eget quam. Donec id elit non mi porta gravida at eget metus. Nullam id dolor id nibh ultricies vehicula ut id elit.</p>
    </div>
    <div style={{ marginTop: 26 }}></div>
    <ul className="collection with-header card">
        <li className="collection-header">
            <h4 className="pink-text"><i className="material-icons orange-text" style={{ fontSize: 36, verticalAlign: 'top' }}>whatshot</i>Popular</h4>
        </li>
        <li className="collection-item avatar">
            <i className="material-icons circle pink lighten-1" style={{ fontSize: 28 }}>image</i>
            <span style={{ fontSize: 16 }}>Awesome Product <small><span className="hide-on-med-and-up"><b>– </b></span> <span className="green-text hide-on-med-and-up"><b>$9.99 USD</b></span><PricePill price={ 9.99 } classes=" hide-on-small-only" showTail /></small></span>
            <br />
            <p>Makes life a blast ...</p>
            <a href="#!" className="secondary-content">
                <i className="material-icons card chip green white-text left hide-on-med-and-up" style={{ marginTop: 20 }}>add_shopping_cart</i>
                <button className="waves-effect waves-light btn btn-large green hide-on-small-only" onClick={action.bind(this)}>
                    <i className="material-icons white-text left">add_shopping_cart</i> Buy
                </button>
            </a>
        </li>
        <li>
            <a href="./products">
                <button className="waves-effect waves-light btn pink lighten-2 col s12">
                    <i className="material-icons" style={{ verticalAlign: 'top' }}>list</i> All Products
                </button>
            </a>
        </li>
        </ul>
  </div>);
};

export default HomePage;



