import React from 'react';
import './FloatingActionMenu.css';

const FloatingActionMenu = () => (
  <div className="fixed-action-btn" style={{ bottom: 45, right: 24 }}> 
      <a className="btn-floating btn-large pink lighten-2"><i className="material-icons">menu</i></a> 
      <ul> 
          <li>
              <a className="btn-floating red" href="./checkout"><i className="material-icons">shopping_cart</i></a>
          </li>         
          <li>
              <a className="btn-floating yellow darken-1" href="./products"> <i className="material-icons">list</i> </a>
          </li>         
          <li>
              <a className="btn-floating green" href="./account"><i className="material-icons">account_circle</i></a>
          </li>         
          <li>
              <a className="btn-floating blue" href="./contact"> <i className="material-icons">mail</i> </a>
          </li>         
          <li>
              <a className="btn-floating orange text-white" href="#!"> <i className="material-icons">arrow_upward</i> </a>
          </li>         
      </ul>     
  </div>
);

export default FloatingActionMenu;



