import axios from 'axios';

export const UPDATE_CART = 'UPDATE_CART';
export const SET_ITEM_QTY_CART = 'SET_ITEM_QTY_CART';
export const INCREMENT_ITEM_QTY_CART = 'INCREMENT_ITEM_QTY_CART';
export const DECREMENT_ITEM_QTY_CART = 'DECREMENT_ITEM_QTY_CART';
export const ADD_ITEM_CART = 'ADD_ITEM_CART';
export const REMOVE_ITEM_CART = 'REMOVE_ITEM_CART';
export const CLEAR_ALL_CART = 'CLEAR_ALL_CART';

export const GET_ALL_PRODUCTS = 'GET_ALL_PRODUCTS';

const BASE_URL = 'http://localhost:5001/api/';
const HOST = 'localhost/api/';
const PORT = 5001;
const API_CONFIG = {
  baseURL: BASE_URL,
  withCredentials: true,
  proxy: {
    host: HOST,
    port: PORT
  }
};

const METHOD_SHOPPINGCART = 'shoppingcart';
const METHOD_SHOPPINGCART_INCREMENT_ITEM_QTY = 'shoppingcart/items/{id}/qty/increment';
const METHOD_SHOPPINGCART_DECREMENT_ITEM_QTY = 'shoppingcart/items/{id}/qty/decrement';
// const METHOD_SHOPPINGCART_SET_ITEM_QTY = 'shoppingcart/items/{id}/qty/{value}';
const METHOD_SHOPPINGCART_ADD_ITEM = 'shoppingcart/items/{id}/add';
const METHOD_SHOPPINGCART_REMOVE_ITEM = 'shoppingcart/items/{id}/remove';
// const METHOD_SHOPPINGCART_DISCOUNT_ADD = 'shoppingcart/discount/{code}/add';
// const METHOD_SHOPPINGCART_DISCOUNT_REMOVE = 'shoppingcart/discount/{code}/remove';
const METHOD_SHOPPINGCART_CLEAR_ALL = 'shoppingcart/clear';
// const METHOD_SHOPPINGCART_SEND_ORDER = 'shoppingcart/send';
// ORDERS
// const METHOD_ORDERS = 'orders';
// const METHOD_ORDERS_RECENT = 'orders/recent/{number}';
// const METHOD_ORDERS_ORDER_BY_ID = 'orders/{id}';
// PRODUCTS
const METHOD_PRODUCTS_GET_ALL = 'products';
// const METHOD_PRODUCTS_PRODUCT_BY_ID = 'products/{id}';
// const METHOD_PRODUCTS_SEARCH = 'products/search/{query}';
// const METHOD_PRODUCTS_FILTER = 'products/filter/{filter}';
// const METHOD_PRODUCTS_SEARCH_AND_FILTER = 'products/filter/{filter}/search/{query}';

export function updateCart(callback) {
  const url = METHOD_SHOPPINGCART;
  const request = axios.get(url, API_CONFIG);
  return (dispatch) => {
    request.then(({ data }) => {
      setTimeout(() => {
        dispatch({
          type: UPDATE_CART,
          payload: data,
        });
        callback();
      }, 16);
    }).catch((error) => {
      dispatch({
        type: UPDATE_CART,
        payload: error,
      });
      callback();
    });
  };
}

// Add to cart
export function addToCart(id, callback) {
  const url = METHOD_SHOPPINGCART_ADD_ITEM.replace('{id}', id);
  const request = axios.get(url, API_CONFIG);
  return (dispatch) => {
    request.then(({ data }) => {
      setTimeout(() => {
        dispatch({
          type: ADD_ITEM_CART,
          payload: data,
        });
        callback();
      }, 16);
    }).catch((error) => {
      dispatch({
        type: ADD_ITEM_CART,
        payload: error,
      });
      callback();
    });
  };
}

// Increment from cart
export function incrementFromCart(id, callback) {
  const url = METHOD_SHOPPINGCART_INCREMENT_ITEM_QTY.replace('{id}', id);
  const request = axios.get(url, API_CONFIG);
  return (dispatch) => {
    request.then(({ data }) => {
      setTimeout(() => {
        dispatch({
          type: INCREMENT_ITEM_QTY_CART,
          payload: data,
        });
        callback();
      }, 1);
    }).catch((error) => {
      dispatch({
        type: INCREMENT_ITEM_QTY_CART,
        payload: error,
      });
      callback();
    });
  };
}

