import React from 'react';
import PropType from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { updateCart } from './../actions';
import './ShoppingCartMenuItem.css';
import { formattedCurrencyString } from '../ShoppingCart/ShoppingCart';

class ShoppingCartMenuItem extends React.Component {
  constructor(props) {
    super(props);
    const { shoppingCart } = this.props;
    this.state = {
      shoppingCart: (shoppingCart.count) ? shoppingCart  : { status: 'loading', count: 0, totalSum: 0.0 },
    };

    this.fetchData = this.fetchData.bind(this);
    this.updateUI = this.updateUI.bind(this);
    this.fetchData();
    setInterval(this.fetchData, 60 * 1000);
  }

  componentDidMount() {
    // this.fetchData();
  }
  
  componentWillUpdate(nextProps, nextState, nextContext) {
    var _self = this;
    if (nextProps.shoppingCart !== _self.props.shoppingCart) {
      this.setState({ loading: true, shoppingCart: nextProps.shoppingCart || { count: 0, totalSum: 0.0 } });
    } else if ( nextProps != this.props) {
      this.setState({ loading: true, shoppingCart: nextProps.shoppingCart || { count: 0, totalSum: 0.0 } });
    }
  }

  fetchData() {
    this.props.updateCart(this.updateUI);
  }

  updateUI() {
    const shoppingCart = this.props.shoppingCart || { status: 'loading', count: 0, totalSum: 0.0 };
    if (shoppingCart && typeof shoppingCart === 'object' && shoppingCart.constructor.name === 'Error') {
      this.setState({ shoppingCart: { status: 'error', count: 0, totalSum: 0.0 }});
      return;
    }
    this.setState({ shoppingCart });
  }

  render() {
    const shoppingCart = this.state.shoppingCart || {};
    // Loading
    if (this.timeoutId !== null) clearTimeout(this.timeoutId);
    if (typeof shoppingCart === 'object' && ((shoppingCart.status === 'loading') || (shoppingCart.status === 'error'))) {
      const _self = this;
      requestAnimationFrame(() => {
        // We'll only execute this code when the user is viewing the site (green).
        if (_self.timeoutId !== null) clearTimeout(_self.timeoutId);
        _self.timeoutId = setTimeout(_self.fetchData, 5 * 1000); // retry in 5 seconds.
      }, _self);
      return (<a className="dropdown-button shopping-cart-item" href="#!" data-activates="dropdown1"><span className="shopping-cart-loader"><i className="cart material-icons left">shopping_cart</i> <i className="rotating-indicator material-icons left">refresh</i></span></a>);
    }
      const { count, total, currencyCode } = this.props;
      // TODO: Move components.
      const formattedTotal = (total) => formattedCurrencyString(total, currencyCode);
      
      const countBadgeStyle = { borderRadius: 10, minWidth: 22, border:'0.5px solid #e50', boxShadow: '#e54 0.5px 1px', marginLeft: -10 };
      const countBadge = (count) => (<span className="badge pink lighten-2 white-text" style={countBadgeStyle}>{ count || 0 }</span>);
      
      const totalBadgeStyle = { borderRadius: 3, border: '0.5px solid #e50', marginLeft: 4 };
      const totalBadge = (total) => {
        return (<span className="badge white blue-text " style={totalBadgeStyle}>{ formattedTotal(total || 0.0) }</span>);
      }
      
      const shoppingCartView = () => {
          if (!this.state.shoppingCart) return "Loading";
          return (<a className="dropdown-button" href="#!" data-activates="dropdown1">
            <span className="left"><i className="material-icons" style={{ fontSize: '2em' }}>shopping_cart</i></span>
            <span>&nbsp;<i className="material-icons right">arrow_drop_down</i> { countBadge((shoppingCart != null) ? shoppingCart.count : count) }{ totalBadge((shoppingCart != null) ? shoppingCart.totalSum : total) }</span></a>);
    };

   return shoppingCartView();
  }
}

ShoppingCartMenuItem.propTypes = {
  currencyCode: PropType.string,
  count: PropType.number,
  total: PropType.number,
  shoppingCart: PropType.object,
};

ShoppingCartMenuItem.defaultProps = {
  count: 0,
  currencyCode: 'USD',
  total: 0.00,
}

const mapDispatchToProps = dispatch => bindActionCreators({ updateCart }, dispatch);

function mapStateToProps({ shoppingCart }) {
  return { shoppingCart };
}

export default connect(mapStateToProps, mapDispatchToProps)(ShoppingCartMenuItem);



